var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('Header',{scopedSlots:_vm._u([{key:"mainpage",fn:function(){return [_c('div',{staticClass:"row row-space"},[_c('div',{staticClass:"col-12"},[_c('div',{staticClass:"row  row-space justify-content-center"},[_c('div',{staticClass:"col-12 text-center"},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("@/assets/img/publicacao/disponiveis.png"),"alt":""}})])]),_c('h3',{staticClass:"text-center text-primary"},[_vm._v("Prática do Evangelho no Lar")]),_c('br'),_c('h5',{staticClass:"text-primary"},[_vm._v("CONCEITO")]),_c('section',{attrs:{"id":"conceito"}},[_c('p',[_vm._v("\n                      O Estudo do Evangelho no Lar é uma reunião em família, em dia e hora da semana determinados, para estudo e conversa sobre os preceitos cristãos, visando o aprofundamento dos conhecimentos doutrinários e a manutenção da harmonia e do equilíbrio no lar.\n                      "),_c('br'),_c('br'),_vm._v(" Adotando a prática ensinada pelo Mestre Jesus, é um encontro familiar e fraternal, do qual participam os mentores espirituais, espíritos afins e demais envolvidos no progresso moral e espiritual dos integrantes do grupo.\n                      "),_c('br'),_vm._v(" Trata-se de uma prática cristã recomendada pela Doutrina Espírita e um poderoso recurso para a limpeza e higiene espiritual do lar.\n\n                      "),_c('br'),_c('br'),_vm._v(" É uma das formas mais saudáveis de fraternidade, que começa na família através do diálogo sincero e do exercício da caridade. Cada lição do Evangelho é um roteiro de luz e de bênçãos para o grupo familiar, para toda a\n                      área circunvizinha do lar que o pratique e de espíritos necessitados de esclarecimentos, trazidos pela Espiritualidade para acompanharem os estudos.\n\n                      "),_c('br'),_c('br')])]),_c('h5',{staticClass:"text-primary"},[_vm._v("IMPORTÂNCIA DO EVANGELHO NO LAR")]),_c('section',{attrs:{"id":"evangelho"}},[_c('p',[_vm._v("\n                      O Estudo do Evangelho no Lar abre as portas da nossa casa aos benefícios espirituais, contribuindo para os trabalhos de amparo e intuição dos mentores espirituais aos membros da família.\n\n                      "),_c('br'),_c('br'),_vm._v(" A busca pela compreensão dos ensinamentos cristãos, com sua absorção através da razão, nos permite o entendimento dos acontecimentos de nossas vidas, oferecendo-nos ânimo para enfrentar as vicissitudes e diretrizes para\n                      que façamos as melhores escolhas para as questões da vida terrena.\n\n                      "),_c('br'),_c('br'),_vm._v(" Através da prática disciplinada e séria do Evangelho, modificamos e melhoramos o padrão vibratório dos nossos pensamentos e sentimentos, desanuviando as nossas mentes congestionadas pelas criações inferiores, agentes da\n                      enfermidade e dos desequilíbrios. Com Jesus no Lar, pelo estudo e vivência do Evangelho, tem-se a verdadeira paz.\n\n                      "),_c('br'),_c('br'),_vm._v(" Com o Evangelho no Lar formamos as defesas magnéticas da nossa casa, envolvendo o ambiente espiritual em energias positivas que desestimulam toda ação maléfica. É uma verdadeira segurança espiritual que passa a funcionar\n                      em benefício de todo o grupo.\n\n                      "),_c('br'),_c('br'),_vm._v(" Além da ajuda que essa prática proporciona no programa espiritual de todo o grupo familiar, estende a caridade aos vizinhos e a quantos se sintam também estimulados a mudar com o exemplo, além de socorrer espíritos diversos\n                      igualmente beneficiados pela fonte de luz!\n                      "),_c('br'),_c('br')])]),_c('h5',{staticClass:"text-primary"},[_vm._v("ORIENTAÇÕES PARA REALIZAÇÃO DO EVANGELHO NO LAR")]),_c('br'),_c('section',{staticClass:"orientacoes"},[_c('p',[_vm._v("\n                      Determine um dia e uma hora da semana em que seja possível a presença de todos os membros da família ou daqueles que desejarem participar da reunião.\n                      "),_c('br'),_c('br'),_vm._v(" O dia e horário definidos devem ser entendidos como compromisso sério, firmado entre o grupo familiar e a espiritualidade, portanto, devem ser cumpridos rigorosa e disciplinarmente, a fim de permitir a efetiva participação\n                      dos mentores espirituais e de espíritos superiores.\n                      "),_c('br'),_c('br'),_vm._v(" A reunião deve ser iniciada com uma prece simples e espontânea num local da casa menos exposto às perturbações exteriores, fazendo-se em seguida a leitura de um trecho de \"O Evangelho Segundo o Espiritismo\", aberto ao\n                      acaso ou previamente programado para estudo em sequência.\n                      "),_c('br'),_c('br'),_vm._v(" A fim de oferecer maior dinâmica à reunião, sugere-se que a leitura seja coletiva e em voz alta, realizada por quantos integrantes sentirem-se à vontade a fazê-la, dedicando-se cada um a um parágrafo do texto por vez.\n                      "),_c('br'),_vm._v(" É importante que a leitura seja feita em voz alta e clara, para que os ensinamentos alcancem também os irmãos desencarnados trazidos pelo Plano Espiritual a participarem dos estudos.\n                      "),_c('br'),_c('br'),_vm._v(" Os participantes devem promover breve e saudável debate sobre o texto lido, explorando o entendimento de cada um sobre a aplicação dos ensinamentos na vida diária, bem como a colocação de questões a serem esclarecidas\n                      ou pontos de vista a serem compartilhados.\n                      "),_c('br'),_c('br'),_vm._v(" Não deve haver discussões, críticas e julgamento entre os membros do grupo. O debate de ideias deve ocorrer à luz da Doutrina e eventuais dúvidas levantadas devem ter seu esclarecimento pesquisado e trazido por um dos\n                      membros em próximos encontros.\n                      "),_c('br'),_c('br'),_vm._v(" A reunião deve ser dirigida por um membro da família ou pela pessoa que tiver mais conhecimento doutrinário, que deverá estimular a participação de todos e conduzir as explicações ao nível do entendimento prático dos presentes.\n                      Pode-se promover a leitura de outras publicações voltadas à Doutrina (“O Livro dos Espíritos”, “O Livro dos Médiuns”, “O que é o Espiritismo”, “A Gênese”, “O Céu e o Inferno” etc).\n                      "),_c('br'),_c('br'),_vm._v(" Sugere-se que seja colocada toalha branca sobre a mesa e pode-se ainda colocar água em uma jarra sobre a mesma, para que seja fluidificada pelos Espíritos presentes, no transcorrer da reunião. Música suave pode contribuir\n                      para melhor ambientação, auxiliando as vibrações e preces.\n                      "),_c('br'),_c('br'),_vm._v(" Após a leitura do texto e o debate entre os participantes, deve ser proferida prece de encerramento, agradecendo pela assistência dos mentores espirituais e pelos bálsamos depositados na água pelos mesmos, os quais nos\n                      auxiliarão na busca da evolução espiritual através da verdadeira reforma íntima, pedindo que os bons fluidos produzidos na reunião permaneçam em cada um dos integrantes e em todo o lar, até a próxima reunião.\n                      "),_c('br'),_c('br'),_vm._v(" Recomenda-se que a duração seja de 30 a 45 minutos, a contar da prece de abertura à de encerramento.\n                      "),_c('br'),_c('br'),_vm._v(" Vale esclarecer que, ainda que somente um integrante do lar deseje introduzir a Prática do Evangelho, a realização é possível. Ressaltando que poderá estar solitário do ponto de vista da companhia de encarnados, entretanto\n                      estará acompanhado de Irmãos de Luz da Espiritualidade, os quais se regozijarão com a iniciativa do estudo e da busca pela reforma íntima.\n                      "),_c('br'),_c('br'),_vm._v(" Com o tempo, o exemplo da prática e os resultados benéficos do concurso em favor dos valores cristãos, motivarão outros membros da família a juntarem-se aos estudos. Nossos votos de muita Paz!\n\n                  ")])]),_c('h6',{staticClass:"text-primary"},[_vm._v("Acolher")]),_c('h6',{staticClass:"font-weigth-bold"},[_vm._v("Instituto Kardecista de Estudos e Amparo")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-md-2"},[_c('img',{staticClass:"img-fluid align-center",attrs:{"src":require("@/assets/logo.png"),"alt":""}})])]),_c('br'),_c('h3',{staticClass:"text-center text-primary"},[_vm._v("Vibrações\n              ")]),_c('br'),_c('section',[_c('p',[_vm._v("\n                      Elevemos o nosso pensamento a Deus, através de Jesus, e solicitemos ao Divino Mestre a permissão para acompanha-Lo com os nossos corações, distribuindo vibrações de amor neste momento:\n                      "),_c('br'),_c('br'),_vm._v(" Vibremos com Jesus!\n                      "),_c('br'),_vm._v(" Pela paz no nosso planeta;\n                      "),_c('br'),_vm._v(" pelos enfermos encarnados e pelos moribundos;\n                      "),_c('br'),_vm._v(" pelos sofredores desencarnados que se encontram na Crosta, no Umbral e nas Trevas;\n                      "),_c('br'),_vm._v(" pela confraternização religiosa;\n                      "),_c('br'),_vm._v(" pela expansão do Evangelho como norma de conduta;\n                      "),_c('br'),_vm._v(" pelos espíritos que estão reencarnando com tarefas evangélicas definidas;\n                      "),_c('br'),_vm._v(" pelos trabalhadores do bem;\n                      "),_c('br'),_vm._v(" por nossos guias e mentores espirituais;\n                      "),_c('br'),_vm._v(" pelo triunfo do bem em relação ao mal como guia dos nossos pensamentos e ações;\n                      "),_c('br'),_vm._v(" por esta casa que nos acolhe;\n                      "),_c('br'),_vm._v(" por todas as creches, asilos, orfanatos, hospitais, sanatórios e cadeias;\n            